import $ from 'jquery';

var mInputTextArea = function (element, options) {
  this.$element = $(element);
  this.uiid = this.$element.attr('id');
  this.default = {
    item: [{ left_value: '', right_value: '' }],
    item_min: 1,
    item_max: 3,
    title: { left: '', right: '' },
    columnClass: { left: '', right: '', btn: '' },
    rows: { left: 2, right: 2 },
    placeholder: { left: '', right: '' },
    spellcheck: false,
    clearmode: true,
    resize: true,
    saveInputHeight: false,
    expand: { active: false, icon: '' },
    buttonAlign: 'center',
  };
  this.options = Object.assign({}, this.default, options);
  this.clearmode = true;

  if(options.clearmode !== undefined && typeof options.clearmode === 'boolean') {
    this.clearmode = options.clearmode;
  }

  this.$element.on('click', '.minputtextarea-btn.addbtn', $.proxy(function(ev) {
    this.addItem(ev.currentTarget);
  }, this));

  this.$element.on('click', '.minputtextarea-btn.delbtn', $.proxy(function(ev) {
    this.deleteItem(ev.currentTarget);
  }, this));

  this.$element.on('click', '.minputtextarea-btn.expandbtn', $.proxy(function(ev) {
    this.expandItem(ev.currentTarget);
  }, this));

  this.$element.on('click', '.minputtextarea-btn.expandbtn', $.proxy(function(ev) {
    this.expandItem(ev.currentTarget);
  }, this));

  this.$element.on('change', '.minputtextarea-input', $.proxy(function(ev) {
    if (this.options.onAction && (typeof this.options.onAction === 'function')) {
      this.options.onAction(this.uiid, 'change', parseInt($(ev.currentTarget).attr('data-item')), {}, []);
    }
  }, this));

  this.populate();
};

mInputTextArea.prototype = {
  clear: function clear() {
    this.options = this.default;
    this.populate();
  },

  destroy: function destroy() {
    this.$element.find('table.minputtextarea').off();
    this.$element.find('table.minputtextarea').remove();
    let data = this.$element.data('daai.inputtextareatable');
    this.$element.removeData('daai.inputtextareatable');
    data = this.$element.data('daai.inputtextareatable');
    this.$element[0].outerHTML = '';
  },

  update: function update(options) {
    this.options = Object.assign({}, this.default, options);
    this.populate();
  },

  reset: function reset() {
    this.options.item = [{ left_value: '', right_value: '' }];
    this.populate();
  },

  populate: function populate() {
    let html = this.render();
    this.$element.html(html);

    $('.minputtextarea-input').each(function () {
      let prev_height = $(this).attr('data-prev-height');
      $(this).css('min-height', $(this).outerHeight());
      if (prev_height) {
        $(this).css('height', prev_height);
      }
    });

    // update buttons width
    $('#' + this.uiid).find('.minputtextarea-btns').each(function () {
      $(this).css('width', ($(this).find('.minputtextarea-btn').length * 20) + 12);
    });

    if (!this.clearmode) {
      this.itemBannedAction();
    }
  },

  render: function render() {
    let da = this.options;
    let html = '';
    html += `<table class="minputtextarea" id="${this.uiid}">`;

    if (da.title) {
      html += `
      <thead class="minputtextarea-header">
        <tr>
          <td class="minputtextarea-title minputtextarea-title-left" id="${this.uiid}-title-left">${da.title.left}</td>
          <td class="minputtextarea-title minputtextarea-title-right" id="${this.uiid}-title-right">${da.title.right}</td>
          <td class="minputtextarea-title minputtextarea-title-btn" id="${this.uiid}-title-btn"></td>
        </tr>
      </thead>`;
    }
    html += '<tbody class="minputtextarea-body">';
    for (let i = 0, len = da.item.length > da.item_max ? da.item_max : da.item.length; i < len; i++) {
      let left_prev_height = (da.saveInputHeight && da.item[i].left_height) ? da.item[i].left_height : '';
      let right_prev_height = (da.saveInputHeight && da.item[i].right_height) ? da.item[i].right_height : '';
      // id="${this.uiid}-input-left-${i}"
      // id="${this.uiid}-input-right-${i}"
      html += `
      <tr class="minputtextarea-item ${this.uiid}-item" data-item="${i}">
        <td class="minputtextarea-td ${da.columnClass.left}">
          <input class="minputtextarea-input ${this.uiid}-input-left" data-item="${i}"
            placeholder="${da.placeholder.left}" spellcheck="${da.spellcheck}"
            data-prev-height="${left_prev_height}" value="${da.item[i].left_value.replace(/"/g, '&quot;')}">
        </td>
        <td class="minputtextarea-td ${da.columnClass.right}">
          <textarea class="minputtextarea-input ${this.uiid}-input-right ${!da.resize ? 'noresize' : ''}"
            data-item="${i}" rows="${da.rows.right}"
            placeholder="${da.placeholder.right}" spellcheck="${da.spellcheck}"
            data-prev-height="${right_prev_height}">${da.item[i].right_value.replace(/"/g, '&quot;')}</textarea>
        </td>
        <td class="minputtextarea-td minputtextarea-btns ${da.columnClass.btn}" style="align-items: ${da.buttonAlign};">
          ${da.expand.active ? `<span class="minputtextarea-btn expandbtn" data-item="${i}"><i class="fas ${da.expand.icon}"></i></span>` : ''}
          <span class="minputtextarea-btn addbtn" data-item="${i}"><i class="fas fa-plus"></i></span>
          <span class="minputtextarea-btn delbtn" data-item="${i}"><i class="fas fa-minus"></i></span>
          <span></span>
        </td>
      </tr>`;
    }
    html += `</tbody>
    </table>`;
    return html;
  },

  addItem: function addItem(el) {
    //*/
    let item = parseInt($(el).attr('data-item'));
    let items = this.getCurrentItems();
    if (!$(el).hasClass('addbtn') || items.length >= this.options.item_max) {
      return;
    }
    let curitem = this.$element.find(`tr[data-item="${item}"]`);
    if (curitem) {
      for (let i = this.options.item_max - 1; i > item; i--) {
        this.$element.find(`tr [data-item="${i}"]`).attr('data-item', i + 1);
        this.$element.find(`tr[data-item="${i}"]`).attr('data-item', i + 1);;
      }

      items.splice(item + 1, 0, { left_value: '', right_value: '', left_height: '', right_height: '' });
      curitem.after(
        `<tr class="minputtextarea-item ${this.uiid}-item" data-item="${item + 1}">
        <td class="minputtextarea-td ${this.options.columnClass.left}">
          <input class="minputtextarea-input ${this.uiid}-input-left" data-item="${item + 1}"
            placeholder="${this.options.placeholder.left}" spellcheck="${this.optionsspellcheck}"
            data-prev-height="" value="">
        </td>
        <td class="minputtextarea-td ${this.options.columnClass.right}">
          <textarea class="minputtextarea-input ${this.uiid}-input-right ${!this.options.resize ? 'noresize' : ''}"
            data-item="${item + 1}" rows="${this.options.rows.right}"
            placeholder="${this.options.placeholder.right}" spellcheck="${this.options.pellcheck}"
            data-prev-height=""></textarea>
        </td>
        <td class="minputtextarea-td minputtextarea-btns ${this.options.columnClass.btn}" style="align-items: ${this.options.buttonAlign};">
          ${this.options.expand.active ? `<span class="minputtextarea-btn expandbtn" data-item="${item + 1}"><i class="fas ${this.options.expand.icon}"></i></span>` : ''}
          <span class="minputtextarea-btn addbtn" data-item="${item + 1}"><i class="fas fa-plus"></i></span>
          <span class="minputtextarea-btn delbtn" data-item="${item + 1}"><i class="fas fa-minus"></i></span>
          <span></span>
        </td>
      </tr>`
      )
      if (this.options.onAction && (typeof this.options.onAction === 'function')) {
        this.options.onAction(this.uiid, 'additem', item, { left_value: '', right_value: ''}, items);
      }
      this.options.item = items;
    }

    /*/
    let item = parseInt($(el).attr('data-item'));
    let items = this.getCurrentItems();
    if ($(el).hasClass('addbtn')) {
      if (items.length < this.options.item_max) {
        items.splice(item + 1, 0, { left_value: '', right_value: '', left_height: '', right_height: '' });
      }
    }

    if (this.options.onAction && (typeof this.options.onAction === 'function')) {
      this.options.onAction(this.uiid, 'additem', item, items[item], items);
    }

    this.options.item = items;
    this.populate();
    //*/
  },

  deleteItem: function deleteItem(el) {
    let item = parseInt($(el).attr('data-item'));
    let items = this.getCurrentItems();

    if ($(el).hasClass('delbtn')) {
      if (items.length > this.options.item_min) {
        let rmitem = this.$element.find(`tr[data-item="${item}"]`);
        if (rmitem) {
          rmitem.remove();
        }
        for (let i = item + 1; i < this.options.item_max; i++) {
          this.$element.find(`tr [data-item="${i}"]`).attr('data-item', i - 1);
          this.$element.find(`tr[data-item="${i}"]`).attr('data-item', i - 1);;
        }
        items.splice(item, 1);
        if (this.options.onAction && (typeof this.options.onAction === 'function')) {
          this.options.onAction(this.uiid, 'deleteitem', item, {}, []);
        }
      } else if (items.length == 1) {
        items = [{ left_value: '', right_value: '', left_height: '', right_height: '' }]; // clear value
      }
    }

    this.options.item = items;
    //this.populate();
  },

  getCurrentItems: function getCurrentItems() {
    let items = [];

    $('.' + this.uiid + '-item').each(function (e) {
      items.push({ left_value: '', right_value: '', left_height: '', right_height: '' });
    });

    $('.' + this.uiid + '-input-left').each(function (e) {
      let item = parseInt($(this).attr('data-item'));
      items[item].left_value = $(this).val().replace(/"/g, '&quot;');
      items[item].left_height = $(this).outerHeight();
    });

    $('.' + this.uiid + '-input-right').each(function (e) {
      let item = parseInt($(this).attr('data-item'));
      items[item].right_value = $(this).val().replace(/"/g, '&quot;');
      items[item].right_height = $(this).outerHeight();
    });

    return items;
  },

  getItems: function getItems() {
    let da = this.getCurrentItems();
    let items = [];

    for (let i = 0, len = da.length; i < len; i++) {
      if (da[i].left_value == '' && da[i].right_value == '') {
        continue;
      }

      items.push({
        left_value: da[i].left_value.replace(/&quot;/g, '"'),
        right_value: da[i].right_value.replace(/&quot;/g, '"')
      });
    }

    return items;
  },

  getAllItems: function getAllItems() {
    let da = this.getCurrentItems();
    let items = [];

    for (let i = 0, len = da.length; i < len; i++) {
      items.push({
        left_value: da[i].left_value.replace(/&quot;/g, '"'),
        right_value: da[i].right_value.replace(/&quot;/g, '"')
      });
    }
    return items;
  },

  getItem: function getItem(index) {
    let da = this.getCurrentItems();
    if (da.length > 0 && index >= 0 && index < da.length) {
      return {
        left_value: da[index].left_value.replace(/&quot;/g, '"'),
        right_value: da[index].right_value.replace(/&quot;/g, '"')
      };
    }
    return { left_value: '', right_value: ''};
  },

  setItem:  function getItem(index, value) {
    if (index >= 0 && index < this.options.item.length) {
      this.options.item[index].left_value = value.left_value;
      this.options.item[index].right_value = value.right_value;

      this.$element.find(`tr[data-item="${index}"] input`).val(value.left_value);
      this.$element.find(`tr[data-item="${index}"] textarea`).val(value.right_value);
    }
  },

  itemBannedAction: function itemBannedAction() {
    if (!this.options.item || !(this.options.item instanceof Array)) {
      this.options.item = [];
    }

    if (this.options.item.length <= this.options.item_min) {
      $('#' + this.uiid).find('.minputtextarea-btn.delbtn').addClass('banned-action');
    } else if (this.options.item.length >= this.options.item_max) {
      $('#' + this.uiid).find('.minputtextarea-btn.addbtn').addClass('banned-action');
    }
  },

  expandItem: function expandItem(el) {
    let index = parseInt($(el).attr('data-item'));
    let data = this.getCurrentItems();
    let item = { left_value: '', right_value: '' };
    let items = [];
    let elements = document.getElementsByClassName(this.uiid + '-item');
    for (let i = 0, len = elements.length; i < len; i++) {
      if (index == $(elements[i]).attr('data-item')) {
        item.left_value = $(elements[i]).find('.' + this.uiid + '-input-left').val();
        item.right_value = $(elements[i]).find('.' + this.uiid + '-input-right').val();
        break;
      }
    }

    // regroup items data
    for (let i = 0, len = data.length; i < len; i++) {
      items.push({ left_value: data[i].left_value, right_value: data[i].right_value });
    }

    if (this.options.onAction && (typeof this.options.onAction === 'function')) {
      this.options.onAction(this.uiid, 'expand', index, item, items);
    }
  },
};

export default mInputTextArea;
