/**
 * The Base class for all Nodes.
 */
class Segments {
  /**
   * @param {object} options
   * @param {object} body
   * @param {Label} labelModule
   * @param captionModule
   */
  constructor(min, max) {
    this.options = { min: min, max: max };
    this.occupation = [];
    this.availability = [{ min: min, max: max }];
  }

  /**
   *
   * @param {array} occupied_segments object with min and max value
   */
  setOccupation(occ) {
    this.occupation.length = 0;
    occ.forEach(elem => {
      if (elem.hasOwnProperty('min') && elem.hasOwnProperty('max') &&
          elem.min <= elem.max &&
          elem.min >= this.options.min && elem.min <= this.options.max &&
          elem.max >= this.options.min && elem.max <= this.options.max) {
        this.occupation.push({ min: elem.min, max: elem.max })
      }
    });
    this.occupation.sort((a, b) => (a.min > b.min ? 1 : -1));
    this._updateAvailability();
    return this;
  }

  /**
   *
   * @param {array} occupied_segments object with min and max value
   */
  getOccupation() {
    return this.occupation;
  }

  /**
  *
  * @param {array} availabile_segments object with min and max value
  */
  setAvailability(avail) {
    this.availability.length = 0;
    avail.forEach(elem => {
      if (elem.hasOwnProperty('min') && elem.hasOwnProperty('max') &&
        elem.min <= elem.max &&
        elem.min >= this.options.min && elem.min <= this.options.max &&
        elem.max >= this.options.min && elem.max <= this.options.max) {
        this.availability.push({ min: elem.min, max: elem.max })
      }
    });
    this.availability.sort((a, b) => (a.min > b.min ? 1 : -1));
    this._updateOccupation();
    return this;
  }

  /**
  *
  * @param {array} availabile_segments object with min and max value
  */
  getAvailability(avail) {
    return this.availability;
  }

  /**
  *
  * @param {object} current_segment Current Segment object with min and max value
  * @param {object} new_segment New Segment object with min and max value
  */
  matchSegment(curSeg, newSeg) {
    if (this._segInSegment(newSeg, this.availability)) {
      return newSeg;
    }

    if (curSeg.min != newSeg.min) {
      const start = this._isPtInSegment(newSeg.min, this.availability, true, true);
      const end = this._isPtInSegment(newSeg.max, this.availability, true, true);
      if (start) {
        const adjpos = this._ptInSegment(newSeg.min, this.availability, true, false);
        if (adjpos) {
          if (this._segInSegment({ min: newSeg.min, max: adjpos.max }, this.availability, true)) {
            return { min: newSeg.min, max: adjpos.max };
          }
          return adjpos;
        }
      } else if (end) {
        const adjpos = this._ptInSegment(newSeg.max, this.availability, false, true);
        if (adjpos) {
          if (this._segInSegment({ min: adjpos.min, max: newSeg.max }, this.availability, true)) {
            return { min: adjpos.min, max: newSeg.max };
          }
          return adjpos;
        }
      }
    } else {
      const start = this._isPtInSegment(newSeg.min, this.availability, true, true);
      const end = this._isPtInSegment(newSeg.max, this.availability, true, true);
      if (end) {
        const adjpos = this._ptInSegment(newSeg.max, this.availability, false, true);
        if (adjpos) {
          if (this._segInSegment({ min: adjpos.min, max: newSeg.max }, this.availability, true)) {
            return { min: adjpos.min, max: newSeg.max };
          }
          return adjpos;
        }
      } else if (start) {
        const adjpos = this._ptInSegment(newSeg.min, this.availability, true, false);
        if (adjpos) {
          if (this._segInSegment({ min: newSeg.min, max: adjpos.max }, this.availability, true)) {
            return { min: newSeg.min, max: adjpos.max };
          }
          return adjpos;
        }
      }
    }

    return (this.availability.length > 0 ? this.availability[0] : curSeg);
  }

  /**
  *
  * @param {array} avail availabile object with min and max value
  */
  _updateAvailability() {
    this.availability.length = 0;
    let pos = this.options.min;
    for (let i = 0, len = this.occupation.length; i < len; i++) {
      if (pos < this.occupation[i].min) {
        this.availability.push({ min: pos, max: this.occupation[i].min });
      }
      pos = this.occupation[i].max;
    }
    if (pos < this.options.max) {
      this.availability.push({ min: pos, max: this.options.max });
    }
  }

  /**
  *
  * @param {array} occ occupied object with min and max value
  */
  _updateOccupation() {
    this.occupation.length = 0;
    let pos = this.options.min;
    for (let i = 0, len = this.availability.length; i < len; i++) {
      if (pos < this.availability[i].min) {
        this.occupation.push({ min: pos, max: this.availability[i].min });
      }
      pos = this.availability[i].max;
    }
    if (pos < this.options.max) {
      this.occupation.push({ min: pos, max: this.options.max });
    }
  }

  /**
  *
  * @param {segment object} x segment object with min and max value
  * @param {segment object array} y array of segment object with min and max value
  * @param {boolean} canTouch include or exclude
  */
  _segInSegment(x, y, canTouch = true) {
    for (let i = 0, len = y.length; i < len; i++) {
      if ((canTouch && x.min >= y[i].min && x.min <= y[i].max && x.max >= y[i].min && x.max <= y[i].max) ||
        (!canTouch && x.min > y[i].min && x.min < y[i].max && x.max > y[i].min && x.max < y[i].max)
      ) {
        return y[i];
      }
    }
    return null;
  }

  /**
  *
  * @param {segment object} x segment object with min and max value
  * @param {segment object array} y array of segment object with min and max value
  * @param {boolean} touchMin include or exclude
  * @param {boolean} touchMax include or exclude
  */
  _ptInSegment(x, y, touchMin = true, touchMax = true) {
    for (let i = 0, len = y.length; i < len; i++) {
      if ((touchMin && x >= y[i].min && touchMax && x <= y[i].max) ||
        (!touchMin && x > y[i].min && touchMax && x <= y[i].max) ||
        (touchMin && x >= y[i].min && !touchMax && x < y[i].max) ||
        (!touchMin && x > y[i].min && !touchMax && x < y[i].max)
      ) {
        return { min: y[i].min, max: y[i].max };
      }
    }
    return null;
  }

  /**
  *
  * @param {segment object} x segment object with min and max value
  * @param {segment object array} y array of segment object with min and max value
  * @param {boolean} touchMin include or exclude
  * @param {boolean} touchMax include or exclude
  */
  _isPtInSegment(x, y, touchMin = true, touchMax = true) {
    for (let i = 0, len = y.length; i < len; i++) {
      if ((touchMin && x >= y[i].min && touchMax && x <= y[i].max) ||
        (!touchMin && x > y[i].min && touchMax && x <= y[i].max) ||
        (touchMin && x >= y[i].min && !touchMax && x < y[i].max) ||
        (!touchMin && x > y[i].min && !touchMax && x < y[i].max)
      ) {
        return true;
      }
    }
    return false;
  }
}

export default Segments;
