import $ from 'jquery';

var mInput = function (element, options) {
  this.$element = $(element);
  this.uiid = this.$element.attr('id');
  this.defaults = {
    item: [{ name: '', value: '' }], length: '', replace: false,
    item_min: 1, item_max: 3, limit: { min: 1, max: 100 },
  }

  this.options = Object.assign({
    item: [{ name: '', value: '' }], length: '', replace: false,
    item_min: 1, item_max: 3, limit: { min: 1, max: 100 },
  }, options);

  this.$element.on('click', '.minput-btn.addbtn', $.proxy(function callToggle(ev) {
    ev.stopImmediatePropagation();
    this.addItem(ev.currentTarget);
  }, this));

  this.$element.on('click', '.minput-btn.delbtn', $.proxy(function callToggle(ev) {
    ev.stopImmediatePropagation();
    this.deleteItem(ev.currentTarget);
  }, this));

  this.$element.on('change', '.minput-input.minput-item-value', $.proxy(function callToggle(ev) {
    this.verifyValueRule(ev.currentTarget);
    this.itemBannedAction();
  }, this));

  this.populate();
};

mInput.prototype = {
  clear: function clear() {
    this.options = {
      item: [{ name: '', value: '', len: '' }], length: '', replace: false,
      item_min: 1, item_max: 3, limit: { min: 1, max: 100 },
    }
    this.setItem();
    this.itemBannedAction();
  },

  destroy: function destroy() {
    this.$element.find('table.minput').off();
    this.$element.find('table.minput').remove();

    let data = this.$element.data('daai.minput');
    this.$element.removeData('daai.minput');
    data = this.$element.data('daai.minput');
    this.$element[0].outerHTML = '';
  },

  update: function update(options) {
    this.options = Object.assign({
      item: [{ name: '', value: '', len: '' }],
      item_min: 1, item_max: 3, limit: { min: 1, max: 100 },
      length: '', replace: false,
    }, options);

    this.setItem();
    this.itemBannedAction();
  },

  populate: function populate() {
    let html = this.render();
    this.$element.html(html);
    this.setItem();
    this.itemBannedAction();
  },

  render: function render() {
    let da = this.options;
    let html = '';
    html += '<table class="minput" id="' + this.uiid + '">';
    html += '  <thead class="minput-header">';
    html += '    <tr>';
    html += '      <td class="minput-title minput-title-left" id="' + this.uiid + '-title-left">Name</td>';
    html += '      <td class="minput-title minput-title-right" id="' + this.uiid + '-title-right">Percent</td>';
    html += '      <td class="minput-title minput-title-len" id="' + this.uiid + '-title-len">Len</td>';
    html += '      <td></td>';
    html += '    </tr>';
    html += '  </thead>';
    html += '  <tbody class="minput-body">';
    for (let i = 0, len = da.item_max; i < len; i++) {
      let hidden = da.item[i] ? ' data-min="' + da.limit.min + '" data-max="' + da.limit.max + '" ' : ' style="display: none;" ';
      html += '<tr class="minput-item ' + this.uiid + '-item" id="' + this.uiid + '-item' + i + '" data-item="' + i + '"' + hidden + '>';
      // name
      html += '  <td class="minput-td">';
      html += '    <input class="minput-input minput-item-name ' + this.uiid + '-input" type="text" id="' + this.uiid + '-input-name' + i + '" value="" data-item="' + i + '">';
      html += '  </td>';
      // value
      html += '  <td class="minput-td">';
      html += '    <input class="minput-input minput-input-sm minput-item-value ' + this.uiid + '-input" type="text" id="' + this.uiid + '-input-value' + i + '" value="" data-item="' + i +
        '" data-previous="" data-min="' + da.limit.min + '" data-max="' + da.limit.max + '" data-length="' + da.length + '">';
      html += '  </td>';
      // len
      html += '  <td class="minput-td">';
      html += '    <input class="minput-input minput-input-sm minput-item-len ' + this.uiid + '-input" type="text" value="" data-item="' + i +
        '" data-length="' + da.length + '" disabled>';
      html += '  </td>';
      html += '  <td class="minput-td minput-btns">';
      html += '    <span class="minput-btn addbtn" data-item="' + i + '" data-curr="' + this.uiid + '-item' + i + '" data-next="' + this.uiid + '-item' + (i + 1) + '"><i class="fas fa-plus"></i></span>';
      html += '    <span class="minput-btn delbtn" data-item="' + i + '" data-curr="' + this.uiid + '-item' + i + '" data-prev="' + this.uiid + '-item' + (i - 1) + '"><i class="fas fa-minus"></i></span>';
      html += '  </td>';
      html += '</tr>';
    }
    html += '  </tbody>'
    html += '</table>';

    return html;
  },

  setItem: function setItem() {
    for (let i = 0, len = this.options.item_max; i < len; i++) {
      $('#' + this.uiid + '-item' + i).removeAttr('style').hide();
      $('#' + this.uiid + '-item' + i).find('.minput-input').val('').attr('data-previous', '');
      $('#' + this.uiid + '-item' + i).find('.minput-btn').removeAttr('style');
    }

    for (let w = 0, ln = this.options.item.length; w < ln; w++) {
      let range = this.verifyRange();
      if (this.options.item[w].value) {
        this.options.item[w].len = Math.floor((this.options.item[w].value * this.options.length) / 100);
      }
      $('#' + this.uiid + '-item' + w).find('.minput-item-name').val(this.options.item[w].name);
      $('#' + this.uiid + '-item' + w).find('.minput-item-value').val(this.options.item[w].value);
      $('#' + this.uiid + '-item' + w).find('.minput-item-len').val(this.options.item[w].len);

      if (w < ln - 1) {
        $('#' + this.uiid + '-item' + w).show().css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        $('#' + this.uiid + '-item' + w).find('.addbtn, .delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '1' });
        // $('#' + this.uiid + '-item' + w).find('.addbtn, .delbtn').removeAttr('style');
      } else if (w == ln - 1) {
        if (!this.options.replace) {
          $('#' + this.uiid + '-item' + w).find('.minput-item-value').attr('data-max', range);
        }
        $('#' + this.uiid + '-item' + w).show().css({ 'cursor': 'auto', 'pointer-events': 'auto', 'opacity': '1' });
        $('#' + this.uiid + '-item' + w).find('.addbtn, .delbtn').removeAttr('style');
      } else if (w > ln - 1) {
        $('#' + this.uiid + '-item' + w).hide();
      }
    }

    let range = this.verifyRange();
    if (!this.options.replace) {
      if (!range || range === 0) {
        $('#' + this.uiid + '-item' + 0).find('.addbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
      }
    }
  },

  addItem: function addItem(el) {
    let curr = $(el).attr('data-curr');
    let next = $(el).attr('data-next');
    let name = $('#' + curr).find('.minput-item-name').val();
    let value = $('#' + curr).find('.minput-item-value').val();
    let range = this.verifyRange();

    // clear next item value
    $('#' + next).find('.minput-item-value').attr('data-previous', '');
    $('#' + next).find('.minput-item').removeAttr('style');
    $('#' + next).find('.minput-input').val('');
    if (!this.options.replace) {
      if (name && value && range > 0) {
        $('#' + curr).css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        $('#' + curr).find('.addbtn, .delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '1' });
        $('#' + next).find('.minput-item-value').attr('data-max', range);
        $('#' + next).show();
      }
    } else {
      if (name && value) {
        $('#' + curr).css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        $('#' + curr).find('.addbtn, .delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '1' });
        $('#' + next).show();
      }
    }
    this.itemBannedAction();
  },

  deleteItem: function deleteItem(el) {
    let curr = $(el).attr('data-curr');
    let prev = $(el).attr('data-prev');
    $('#' + curr).hide();
    $('#' + curr).find('.minput-input').val('');
    $('#' + prev).css({ 'cursor': 'auto', 'pointer-events': 'auto', 'opacity': '1' });
    $('#' + prev).find('.addbtn, .delbtn').removeAttr('style');
    this.itemBannedAction();
  },

  getCurrentItems: function getCurrentItems() {
    let items = [];
    let da = this.options;
    $('.' + this.uiid + '-item').each(function (e) {
      items.push({
        name: '',
        value: '',
        previous_value: '',
        len: ''
      });
    });
    items.sort(function (a, b) {
      return (a.item > b.item ? 1 : -1);
    });

    $('.' + this.uiid + '-input.minput-item-name').each(function (e) {
      let item = parseInt($(this).attr('data-item'));
      let name = $(this).val();
      items[item].name = name;
    });

    let length = this.options.length;
    $('.' + this.uiid + '-input.minput-item-value').each(function (e) {
      let item = parseInt($(this).attr('data-item'));
      let len = length;
      let value = $(this).val() === '' ? '' : Number($(this).val());
      let previous_value = $(this).attr('data-previous');
      items[item].value = value;
      items[item].previous_value = previous_value;
      if (value !== '' && value >= da.limit.min && value <= da.limit.max) {
        items[item].len = Math.floor((len * value) / 100);
      } else {
        items[item].len = '';
      }
    });

    return items;
  },

  getItems: function getItems() {
    let da = this.getCurrentItems();
    let items = [];
    for (let i = 0, len = da.length; i < len; i++) {
      if (da[i].name === '' && da[i].value === '' || da[i].value === 0) {
        continue;
      }
      items.push({
        name: da[i].name,
        value: da[i].value
      });
    }

    // let ret = this.verifyInputName();
    // console.log(ret);
    return items;
  },

  itemBannedAction: function itemBannedAction() {
    let len = $('#' + this.uiid).find('.minput-item:visible').length;
    let da = this.options;
    $('#' + this.uiid).find('.minput-item').each(function (e) {
      if (!($(this).css('display') == 'none')) {
        let idx = $(this).attr('data-item');
        if (len <= da.item_min) {
          $(this).find('.delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        }
        if (len >= da.item_max && idx == (len - 1)) {
          $(this).find('.addbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        }
      }
    });
  },

  verifyValueRule: function verifyValueRule(el) {
    let idx = $(el).attr('data-item');
    let value = $(el).val();
    let min = Number($(el).attr('data-min'));
    let max = Number($(el).attr('data-max'));
    let previous_value = $(el).attr('data-previous');
    let length = this.options.length;
    if (!isNaN(Number(value)) && value !== '' && value >= min && value <= max) {
      $(el).val(Number(value)).attr('data-previous', value);
    } else {
      $(el).val(previous_value);
    }

    let percent = $(el).val() === '' ? '' : Number($(el).val());
    let len = 0;
    len = percent === '' ? '' : Math.floor((percent * length) / 100)
    $(el).parent().next().find('.minput-item-len').val(len);

    if (!this.options.replace) {
      let range = this.verifyRange();
      if (!range || range === 0) {
        $('#' + this.uiid + '-item' + idx).find('.addbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
      } else {
        $('#' + this.uiid + '-item' + idx).find('.addbtn').css({ 'cursor': 'auto', 'pointer-events': 'auto', 'opacity': '1' });
      }
    }
  },

  verifyRange: function verifyRange() {
    let rangelist = [];
    for (let i = 0, len = this.options.item_max; i < len; i++) {
      if (!($('#' + this.uiid + '-item' + i).css('display') == 'none')) {
        let value = Number($('#' + this.uiid + '-item' + i).find('.minput-item-value').val());
        rangelist.push(value);
      }
    }

    let avail = this.getAvailability(rangelist);
    return avail;
  },

  getAvailability: function getAvailability(range) {
    let avail = this.options.limit.max;
    for (let i = 0, len = range.length; i < len; i++) {
      if (range[i] >= this.options.limit.min && range[i] <= this.options.limit.max) {
        avail = avail - range[i];
      }
    }
    return avail;
  },

  verifyInputName: function verifyInputName() {
    let ret = true;
    $('#' + this.uiid).find('.minput-item').each(function (e) {
      if (!($(this).css('display') == 'none')) {
        let name = $(this).find('.minput-input').val();
        if (!name) {
          ret = false;
          $(this).find('.minput-icon').show();
        }
      }
    });

    return ret;
  },
}

export default mInput;
