import $ from 'jquery';
import mSlider from './component/mslider';
import Segments from './component/segments';

let mSliders = function (element, options) {
  this.$element = $(element);
  this.defaults = {
    item_min: 1, item_max: 3, item: [{ name: '', from: 0, to: 100 }], replace: false, limit: { min: 0, max: 100 },
  }

  this.options = Object.assign({
    item_min: 1, item_max: 3, item: [{ name: '', from: 0, to: 100 }], replace: false, limit: { min: 0, max: 100 },
  }, options);

  this.uiid = this.$element.attr('id');

  this.sliders = [];
  this.onSilderFinishCb = this.onSilderFinish.bind(this);
  this.seg = new Segments(this.options.limit.min, this.options.limit.max);

  this.$element.on('click', '.addbtn', $.proxy(function callToggle(ev) {
    ev.stopImmediatePropagation();
    this.addItem(ev.currentTarget);
  }, this));

  this.$element.on('click', '.delbtn', $.proxy(function callToggle(ev) {
    ev.stopImmediatePropagation();
    this.deleteItem(ev.currentTarget);
  }, this));

  this.$element.on('input', '.msliders-input', $.proxy(function callToggle(ev) {
    ev.stopImmediatePropagation();
    $(ev.currentTarget).next().hide();
  }, this));

  this.populate();
}

mSliders.prototype = {
  clear: function clear() {
    this.options = {
      item_min: 1, item_max: 3, item: [{ name: '', from: 0, to: 100 }], replace: false, limit: { min: 0, max: 100 },
    }
    this.setSlider();
    this.itemBannedAction();
  },
  destroy: function clear() {
    this.$element.find('div.msliders-item').remove();
    let data = this.$element.data('daai.msliders');
    this.$element.removeData('daai.msliders');
    data = this.$element.data('daai.msliders');
    this.$element.off();
    return this.$element[0].outerHTML;
  },

  update: function update(options) {
    this.options = Object.assign({
      item_min: 1, item_max: 3, item: [{ name: '', from: 0, to: 100 }], replace: false, limit: { min: 0, max: 100 },
    }, options);

    this.setSlider();
    this.itemBannedAction();
  },

  populate: function populate() {
    let html = this.render();
    this.$element.html(html);
    this.setSlider();
    this.itemBannedAction();
  },

  render: function render() {
    let da = this.options;
    let html = '';
    for (let i = 0, len = da.item_max; i < len; i++) {
      let hidden = da.item[i] ? ' data-min="' + da.limit.min + '" data-max="' + da.limit.max + '" ' : ' style="display: none;" ';
      let value = da.item[i] ? da.item[i].name : '';
      html += '<div class="msliders-item" data-item="' + i + '" id="' + this.uiid + '-item' + i + '"' + hidden + '>';
      html += '  <div class="msliders-header">';
      html += '    <span class="msliders-title">Name</span>';
      html += '    <input class="msliders-input" type="text" value="' + value + '"><i class="fas fa-exclamation-circle msliders-icon" style="display: none;"></i>';
      html += '    <div class="msliders-btns">';
      html += '      <span class="msliders-btn addbtn" data-curr="' + this.uiid + '-item' + i + '" data-next="' + this.uiid + '-item' + (i + 1) + '"><i class="fas fa-plus"></i></span>';
      html += '      <span class="msliders-btn delbtn" data-curr="' + this.uiid + '-item' + i + '" data-prev="' + this.uiid + '-item' + (i - 1) + '"><i class="fas fa-minus"></i></span>';
      html += '    </div>';
      html += '  </div>';
      html += '  <div class="msliders-body">';
      html += '    <input class="msliders-slider" id="' + this.uiid + '-slider' + i + '" type="text" data-item="' + i + '" data-prev-start="0" data-prev-end="0">';
      html += '  </div>';
      html += '</div>';
    }
    return html;
  },

  setSlider: function setSlider() {
    let opt = {};
    for (let i = 0, len = this.options.item_max; i < len; i++) {
      opt = {
        min: this.options.limit.min,
        max: this.options.limit.max,
        from: (this.options.item[i] ? this.options.item[i].from : 0),
        to: (this.options.item[i] ? this.options.item[i].to : 0),
        type: 'double',
        hide_min_max: true,
        keyboard: true,
        step: 1,
        prefix: '',
        force_edges: true,
        grid_num: 10,
        grid: true,
        onFinish: this.onSilderFinishCb, //this.onSilderFinish.bind(this),
      };
      if (!this.sliders[i]) {
        this.sliders[i] = new mSlider('#' + this.uiid + '-slider' + i, opt, 0)
      } else {
        this.sliders[i].update(opt);
      }
      $('#' + this.uiid + '-slider' + i).attr('data-prev-start', opt.from).attr('data-prev-end', opt.to);
      $('#' + this.uiid + '-item' + i).removeClass('item-sep').removeAttr('style').hide();
      $('#' + this.uiid + '-item' + i).find('.msliders-btn').removeAttr('style');
    }

    for (let w = 0, ln = this.options.item.length; w < ln; w++) {
      $('#' + this.uiid + '-item' + w).find('.msliders-input').val(this.options.item[w].name);
      // $('#' + this.uiid + '-item' + w).removeAttr('style');
      if (w < ln - 1) {
        $('#' + this.uiid + '-item' + w).show().css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        $('#' + this.uiid + '-item' + w).find('.addbtn, .delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
      } else if (w == ln - 1) {
        $('#' + this.uiid + '-item' + w).show().css({ 'cursor': 'auto', 'pointer-events': 'auto', 'opacity': '1' });
        $('#' + this.uiid + '-item' + w).find('.addbtn, .delbtn').removeAttr('style');
      } else if (w > ln - 1) {
        $('#' + this.uiid + '-item' + w).hide();
      }
    }
    let range_aval = this.verifySegment();
    if (this.options.replace) {
      return
    } else {
      if (!range_aval || range_aval.length < 1) {
        $('#' + this.uiid + '-item' + 0).find('.addbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
      }
    }
  },

  onSilderFinish: function onSilderFinish(data) {
    let replace = this.options.replace;
    if (replace) {
      this.itemBannedAction();
      return;
    }
    let preSeg = {
      min: parseInt($('#' + data.input[0].id).attr('data-prev-start')),
      max: parseInt($('#' + data.input[0].id).attr('data-prev-end'))
    }
    let newSeg = {
      min: data.from,
      max: data.to
    }

    let range = [];
    let idx = parseInt($('#' + data.input[0].id).attr('data-item'));
    if (idx < 1) {
      range = [];
    } else {
      for (let i = 0, len = idx; i < len; i++) {
        let slider = this.sliders[i];
        range.push({
          min: slider.result.from,
          max: slider.result.to
        });
      }
    }

    let adjpos = this.seg.setOccupation(range).matchSegment(preSeg, newSeg);
    if (adjpos.min != data.from || adjpos.max != data.to) {
      $('#' + data.input[0].id).attr('data-prev-start', adjpos.min).attr('data-prev-end', adjpos.max);
      let idx = $('#' + data.input[0].id).attr('data-item');
      this.sliders[idx].update({
        from: adjpos.min,
        to: adjpos.max
      });
    }

    let range_aval = this.verifySegment();
    if (!range_aval || range_aval.length < 1) {
      $('#' + this.uiid + '-item' + idx).find('.addbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
    } else {
      $('#' + this.uiid + '-item' + idx).find('.addbtn').css({ 'cursor': 'auto', 'pointer-events': 'auto', 'opacity': '1' });
    }
    this.itemBannedAction();
  },

  addItem: function addItem(el) {
    let curr = $(el).attr('data-curr');
    let next = $(el).attr('data-next');
    let name = $('#' + curr).find('.msliders-input').val();
    if (!name) {
      $('#' + curr).find('.msliders-icon').show();
    } else {
      $('#' + curr).find('.msliders-icon').hide();
    }

    $('#' + next).find('.msliders-icon').hide();
    $('#' + next).find('.msliders-item').removeAttr('style');
    $('#' + next).find('.msliders-input').val('');
    let idx = $('#' + next).attr('data-item');
    let slider = this.sliders[idx];
    let range = this.verifySegment();
    // verify name and range -> show item
    if (!this.options.replace) {
      if (name && range.length > 0) {
        $('#' + curr).addClass('item-sep').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        $('#' + curr).find('.addbtn, .delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '1' });
        slider.update({
          from: range[0].min,
          to: range[0].max
        });
        $('#' + next).find('.msliders-slider').attr('data-prev-start', range[0].min).attr('data-prev-end', range[0].max);
        $('#' + next).show();
      }

      // range is empty -> disable addbtn
      let range_aval = this.verifySegment();
      if (!range_aval || range_aval.length < 1) {
        $('#' + next).find('.addbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
      } else {
        $('#' + next).find('.addbtn').css({ 'cursor': 'auto', 'pointer-events': 'auto', 'opacity': '0.3' });
      }
    } else {
      if (name) {
        $('#' + curr).addClass('item-sep').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        $('#' + curr).find('.addbtn, .delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        slider.update({
          from: (range.length > 0) ? range[0].min : 0,
          to: (range.length > 0) ? range[0].max : 100
        });
        $('#' + next).find('.msliders-slider').attr('data-prev-start', 0).attr('data-prev-end', 100);
        $('#' + next).show();
      }
    }

    this.itemBannedAction();
  },

  deleteItem: function deleteItem(el) {
    let curr = $(el).attr('data-curr');
    let prev = $(el).attr('data-prev');
    let idx = $('#' + curr).attr('data-item');
    let slider = this.sliders[idx];
    // let slider = $('#' + curr).find('.msliders-slider').data('mSlider');
    $('#' + prev).removeClass('item-sep').css({ 'cursor': 'auto', 'pointer-events': 'auto', 'opacity': '1' });
    $('#' + curr).hide();
    slider.update({
      from: this.options.limit.min, to: this.options.limit.max
    });

    $('#' + prev).find('.addbtn, .delbtn').removeAttr('style');
    $('#' + curr).find('.msliders-input').val('');
    this.itemBannedAction();
  },

  getItems: function getItems() {
    let item = [];
    for (let i = 0, len = this.options.item_max; i < len; i++) {
      if (!($('#' + this.uiid + '-item' + i).css('display') == 'none')) {
        item.push({ name: '', from: '', to: '' });
        let idx = $('#' + this.uiid + '-item' + i).attr('data-item');
        let name = $('#' + this.uiid + '-item' + i).find('.msliders-input').val();
        let slider = this.sliders[idx];
        item[idx].name = name;
        item[idx].from = slider.result.from;
        item[idx].to = slider.result.to;
      }
    }

    // let ret = this.verifyInputName();
    // console.log(ret);
    return item;
  },

  itemBannedAction: function itemBannedAction() {
    let len = $('#' + this.uiid).find('.msliders-item:visible').length;
    let da = this.options;
    $('#' + this.uiid).find('.msliders-item').each(function (e) {
      if (!($(this).css('display') == 'none')) {
        // $('.addbtn, .delbtn').removeAttr('style');
        if (len <= da.item_min) {
          $(this).find('.delbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        }
        if (len >= da.item_max) {
          $(this).find('.addbtn').css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
        }
      }
    });

    // $('#' + this.uiid).find('.msliders-item:visible').not(':last').each(function (i, e) {
    //   $(this).css({ 'cursor': 'not-allowed', 'pointer-events': 'none', 'opacity': '0.3' });
    // });
  },

  verifySegment: function verifySegment() {
    let rangelist = [];
    for (let i = 0, len = this.options.item_max; i < len; i++) {
      if (!($('#' + this.uiid + '-item' + i).css('display') == 'none')) {
        let idx = $('#' + this.uiid + '-item' + i).attr('data-item');
        let slider = this.sliders[idx];
        rangelist.push({
          min: slider.result.from,
          max: slider.result.to
        });
      }
    }

    let range = this.seg.setOccupation(rangelist).getAvailability();
    return range;
  },

  verifyInputName: function verifyInputName() {
    let ret = true;
    $('#' + this.uiid).find('.msliders-item').each(function (e) {
      if (!($(this).css('display') == 'none')) {
        let name = $(this).find('.msliders-input').val();
        if (!name) {
          ret = false;
          $(this).find('.msliders-icon').show();
        }
      }
    });

    return ret;
  },
}

export default mSliders;
