import $ from 'jquery';

var mOperator = function (element, options) {
  this.$element = $(element);
  this.uiid = this.$element.attr('id');
  this.defaults = {
    item: [{ operator: '', operator_index: 0, value: '' }],
    operator_order: ['-', '>', '<', '==', '>=', '<=', '!='],
    item_min: 1, item_max: 3,
    numerical: true, limit: { min: -Infinity, max: Infinity },
  }

  this.string = {
    Operator: 'Operator', Value: 'Value'
  }

  if (options.string) {
    this.string = options.string;
  }

  this.options = Object.assign({
    item: [{ operator: '', operator_index: 0, value: '' }],
    operator_order: ['-', '>', '<', '==', '>=', '<=', '!='],
    item_min: 1, item_max: 3,
    numerical: true, limit: { min: -Infinity, max: Infinity },
  }, options);

  this.$element.on('click', '.moperator-btn.addbtn', $.proxy(function callToggle(ev) {
    ev.stopImmediatePropagation();
    this.addItem(ev.currentTarget);
  }, this));

  this.$element.on('click', '.moperator-btn.delbtn', $.proxy(function callToggle(ev) {
    ev.stopImmediatePropagation();
    this.deleteItem(ev.currentTarget);
  }, this));

  this.$element.on('change', '.moperator-input', $.proxy(function callToggle(ev) {
    // ev.stopImmediatePropagation();
    if (this.options.numerical) {
      this.verifyValueRule(ev.currentTarget);
    }
  }, this));

  this.populate();
};

mOperator.prototype = {
  clear: function clear() {
    this.options = {
      item: [{ operator: '', operator_index: 0, value: '' }],
      operator_order: ['-', '>', '<', '==', '>=', '<=', '!='],
      item_min: 1, item_max: 3,
      numerical: true, limit: { min: -Infinity, max: Infinity },
    }
    this.populate();
  },
  destroy: function destroy() {
    this.$element.find('table.moperator').off();
    this.$element.find('table.moperator').remove();

    let data = this.$element.data('daai.operationtable');
    this.$element.removeData('daai.operationtable');
    data = this.$element.data('daai.operationtable');
    this.$element[0].outerHTML = '';
  },
  update: function update(options) {
    this.options = Object.assign({
      item: [{ operator: '', operator_index: 0, value: '' }], operator_order: this.options.operator_order,
      item_min: 1, item_max: 3,
      numerical: true, limit: { min: -Infinity, max: Infinity },
    }, options);

    this.populate();
  },
  resetTable: function resetTable() {
    this.options.item = [{
      operator: '', operator_index: 0, value: ''
    }]

    let html = this.render();
    this.$element.html(html);
    this.itemBannedAction();
  },
  populate: function populate() {
    let html = this.render();
    this.$element.html(html);
    this.itemBannedAction();
  },
  render: function render() {
    let da = this.options;
    for (let i = 0, len = da.item.length; i < len; i++) {
      let sel = da.operator_order.indexOf(da.item[i].operator);
      da.item[i].operator_index = (sel == -1 ? 0 : sel);
    }

    let html = '';
    html += '<table class="moperator" id="' + this.uiid + '">';
    html += '  <thead class="moperator-header">';
    html += '    <tr>';
    html += '      <td class="moperator-title moperator-title-left" id="' + this.uiid + '-title-left">' + this.string.Operator + '</td>';
    html += '      <td class="moperator-title moperator-title-right" id="' + this.uiid + '-title-right">' + this.string.Value + '</td>';
    html += '      <td></td>';
    html += '    </tr>';
    html += '  </thead>';
    html += '  <tbody class="moperator-body">';
    for (let i = 0, len = (da.item.length > da.item_max ? da.item_max : da.item.length); i < len; i++) {
      html += '<tr class="moperator-item ' + this.uiid + '-item" data-item="' + i + '">';
      html += '  <td class="moperator-td">';
      html += '    <select class="moperator-select ' + this.uiid + '-select" data-item="' + i + '">';
      for (let w = 0, ln = da.operator_order.length; w < ln; w += 1) {
        html += '      <option value="' + w + '" ' + (da.item[i].operator_index == w ? ' selected' : '') + '>' + da.operator_order[w] + '</option>';
      }
      html += '    </select>';
      html += '  </td>';
      html += '  <td class="moperator-td">';
      if (da.numerical) {
        html += '    <input class="moperator-input ' + this.uiid + '-input" type="text" value="' + da.item[i].value + '" data-item="' + i +
          '" data-previous="' + (da.item[i].previous_value ? da.item[i].previous_value : '') + '" data-min="' + (da.limit.min ? da.limit.min : -Infinity) + '" data-max="' + (da.limit.max ? da.limit.max : Infinity) + '">';
      } else {
        html += '    <input class="moperator-input ' + this.uiid + '-input" type="text" value="' + da.item[i].value + '" data-item="' + i + '">';
      }
      html += '  </td>';
      html += '  <td class="moperator-td moperator-btns">';
      html += '    <span class="moperator-btn addbtn" data-item="' + i + '"><i class="fas fa-plus"></i></span>';
      html += '    <span class="moperator-btn delbtn" data-item="' + i + '"><i class="fas fa-minus"></i></span>';
      html += '  </td>';
      html += '</tr>';
    }
    html += '  </tbody>'
    html += '</table>';

    return html;
  },

  addItem: function addItem(el) {
    let item = parseInt($(el).attr('data-item'));
    let items = this.getCurrentItems();
    if ($(el).hasClass('addbtn')) {
      if (items.length < this.options.item_max) {
        items.splice(item + 1, 0, {
          operator: '', operator_index: 0, value: ''
        });
      }
    }

    this.options.item = items;
    let html = this.render();
    this.$element.html(html);
    this.itemBannedAction();
  },

  deleteItem: function deleteItem(el) {
    let item = parseInt($(el).attr('data-item'));
    let items = this.getCurrentItems();
    if ($(el).hasClass('delbtn')) {
      if (items.length > this.options.item_min) {
        items.splice(item, 1);
      }
    }

    this.options.item = items;
    let html = this.render();
    this.$element.html(html);

    this.itemBannedAction();
  },

  getCurrentItems: function getCurrentItems() {
    let items = [];
    $('.' + this.uiid + '-item').each(function (e) {
      items.push({
        operator: '',
        operator_index: 0,
        value: '',
        previous_value: ''
      });
    });
    items.sort(function (a, b) {
      return (a.item > b.item ? 1 : -1);
    });

    $('.' + this.uiid + '-select').each(function (e) {
      let item = parseInt($(this).attr('data-item'));
      let operator = $(this).find('option:selected').text();
      items[item].operator = operator;
    });

    $('.' + this.uiid + '-input').each(function (e) {
      let item = parseInt($(this).attr('data-item'));
      let value = $(this).val();
      let previous_value = $(this).attr('data-previous');
      items[item].value = value;
      items[item].previous_value = previous_value;
    });

    return items;
  },

  getItems: function getItems() {
    let da = this.getCurrentItems();
    let tmp = [];
    let items = [];

    for (let i = 0, len = da.length; i < len; i++) {
      tmp.push({
        operator: '',
        operator_index: 0,
        value: ''
      });
      let sel = this.options.operator_order.indexOf(da[i].operator)
      tmp[i].operator_index = sel == -1 ? 0 : sel;
      tmp[i].operator = da[i].operator;
      tmp[i].value = da[i].value;
    }

    for (let w = 0, ln = tmp.length; w < ln; w++) {
      if (tmp[w].operator == '-' && tmp[w].value == '') {
        continue;
      }
      items.push({
        operator: tmp[w].operator,
        operator_index: tmp[w].operator_index,
        value: tmp[w].value
      })
    }
    return items;
  },

  itemBannedAction: function itemBannedAction() {
    if (!this.options.item || this.options.item.length < 1) {
      this.options.item = [];
    }

    if (this.options.item.length <= this.options.item_min) {
      $('#' + this.uiid).find('.moperator-btn.delbtn').addClass('banned-action');
    } else if (this.options.item.length >= this.options.item_max) {
      $('#' + this.uiid).find('.moperator-btn.addbtn').addClass('banned-action');
    }
  },

  verifyValueRule: function verifyValueRule(el) {
    let value = Number($(el).val());
    let min = Number($(el).attr('data-min'));
    let max = Number($(el).attr('data-max'));
    let previous = $(el).attr('data-previous');

    value = parseInt(value);
    if (!isNaN(Number(value)) && value >= min && value <= max) {
      $(el).attr('data-previous', value);
    } else {
      $(el).val(previous);
    }
  },
}

export default mOperator;
